body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

:root {
  --blue-color: #00acdc;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  flex-shrink: 0;
  max-width: 40px;
}

.logo_img {
  padding: 10px 0;
  display: block;
}

.nav__list {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

